@font-face {
    font-family: 'Roboto';
    src: url('../fonts/Roboto-Regular.woff') format('woff');
}

.container {
    display: flex;
    justify-content: center;
    min-height: 70vh;
    margin-top: 50px;
    font-family: 'Roboto', sans-serif;
}

.calories {
    width: 250px;
    border: 2px solid #F29279;
    border-radius: 20px;
    text-align: center;
    border-right: none;
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
}

.container h2 {
    font-weight: normal;
    font-size: 18px;
    line-height: 1.2;
    margin-top: 25px;
}

/*Все тексты внутри AppContainer будем писать шрифтом Roboto. Как и в предыдущем задании, нужно применить
новый шрифт к CSS-правилу для селектора тега .container.*/