@font-face {
    font-family: 'Sumana';
    src: url('../fonts/Sumana-Regular.woff') format('woff');
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    flex-direction: column;
}

.header__logo {
    width: 100px;
}

.header__header {
    font-size: 44px;
    font-family: 'Sumana', sans-serif;
    margin: 5px 0 0;
    line-height: 1;
}